import { useEffect, useState } from 'react';
import { CircularProgress } from '@mui/material'; // Asegúrate de importar esto correctamente
import Sidebar from './Sidebar';
import MainPanel from './MainPanel';
import ModalCreateBoard from './ModalCreateBoard';
import ModalCreateChart from './ModalCreateChart';
import ModalEditBoard from './ModalEditBoard';
import ModalConfirm from './ModalConfirm';
import { useBoardStore } from './store'; // Asegúrate de importar tu store correctamente

export default function Home() {
  const { loadData } = useBoardStore.getState();

  const [isLoading, setIsLoading] = useState(true);
  const [forceRefresh, setForceRefresh] = useState(false);

  function Refresh() {
    setForceRefresh(!forceRefresh);
  }

  useEffect(() => {
    loadData();
    setIsLoading(false);
  }, [loadData]);

  return (
    <section className="flex flex-col w-full h-full items-center justify-center gap-4 py-8 md:py-10">
      <div className="flex flex-col md:flex-row w-full min-h-[720px]">
        {isLoading ? (
          <div className="flex w-full h-full justify-center items-center">
            <CircularProgress />
          </div>
        ) : (
          <div className="flex flex-col md:flex-row w-full h-full -mt-10">
            <Sidebar refresh={Refresh} />
            <MainPanel refresh={Refresh} />
          </div>
        )}
      </div>
      <div className="flex flex-col space-y-4 mt-4">
        <ModalCreateBoard refresh={Refresh} />
        <ModalCreateChart refresh={Refresh} />
        <ModalEditBoard refresh={Refresh} />
        <ModalConfirm refresh={Refresh} />
      </div>
    </section>
  );
}